@import url(https://fonts.googleapis.com/css?family=Poppins:300,400&display=swap);
html, body {
  background-color: #282c34;
  font-family: 'Poppins', sans-serif;
  color: #afafaf;
  margin: 0;
  height: 100vh;
  overflow-x: hidden;
}

/* from https://css-tricks.com/custom-scrollbars-in-webkit/ */

::-webkit-scrollbar {
  width: 0.8vw;
}

::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #21252b;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #a6acb7;
}

/* some style here uses css taken from https://github.com/mdo/wtf-forms/ */
.FakeCodeTyper {
  width: 100vw;
  height: 100%; }
  .FakeCodeTyper .InfoIcon {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 99;
    margin-top: 15px;
    margin-right: 20px;
    cursor: pointer; }
    .FakeCodeTyper .InfoIcon img {
      width: 32px;
      height: 32px;
      -webkit-filter: brightness(50%);
              filter: brightness(50%); }
  .FakeCodeTyper .CodeDisplay {
    height: 100vh;
    width: 100vw; }

@charset "UTF-8";
.CodeDisplay code {
  font-family: 'Fira Code', 'Menlo', 'Monaco', 'Courier New', Courier, monospace;
  font-size: 18px;
  overflow-x: hidden; }

@-webkit-keyframes blinker {
  50% {
    opacity: 0; } }

@keyframes blinker {
  50% {
    opacity: 0; } }
  .CodeDisplay code::after {
    content: "│";
    font-weight: 300;
    -webkit-animation: blinker 1s linear infinite;
            animation: blinker 1s linear infinite; }

.CodeDisplay pre {
  width: 100%;
  height: 100%;
  margin: 0;
  overflow: auto; }

.InfoPanel {
  position: absolute;
  top: 0;
  right: 0;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding-right: 25px;
  height: 100%;
  overflow-y: auto;
  background-color: #333639;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  .InfoPanel h1 {
    margin: 8px 15px 0 20px; }
  .InfoPanel h2 {
    margin: 30px 15px 0 20px;
    font-weight: normal; }
  .InfoPanel button {
    margin: 8px 15px 0 20px;
    padding: 8px 0;
    width: 15vw;
    border: none;
    font-family: inherit;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    background-color: #505357;
    color: #e6e6e6;
    border-radius: 5px; }
    .InfoPanel button:hover {
      background-color: #646464; }
  .InfoPanel select {
    margin: 8px 15px 0 20px;
    width: 15vw;
    border: none;
    font-family: inherit;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    background-color: #505357;
    color: #e6e6e6;
    border-radius: 5px; }
  .InfoPanel input[type="number"] {
    margin: 8px 15px 0 20px;
    padding: 3px 8px;
    width: 15vw;
    border: none;
    font-family: inherit;
    font-size: 14px;
    font-weight: bold;
    background-color: #505357;
    color: #e6e6e6;
    border-radius: 5px; }
  .InfoPanel #file {
    display: none; }
  .InfoPanel p {
    margin-bottom: 0; }
  .InfoPanel .footer {
    font-size: 16px;
    font-weight: lighter;
    margin-top: 2vh;
    padding-left: 20px; }
    .InfoPanel .footer span {
      font-size: 10px; }
  .InfoPanel .description {
    margin: 0;
    margin-left: 20px; }
    .InfoPanel .description p {
      margin: 0;
      font-size: 13px;
      font-weight: lighter; }

a {
  color: inherit;
  -webkit-text-decoration-style: dotted;
          text-decoration-style: dotted; }
  a:hover {
    color: #e6e6e6;
    -webkit-text-decoration-style: solid;
            text-decoration-style: solid; }

